@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  margin-top: 40px;
  line-height: normal;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  font-style: normal;
  background-color: #ffffff;
  color: #000000;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  margin-left: 0;
  width: 40px;
  height: 40px;
  line-height: 20px;
  padding: 10px 0;
  text-decoration: none;
  vertical-align: middle;
  border: none;
  border-radius: 5px;
  background-color: #ffffff;

  -moz-transition: all 500ms ease-out;
  -o-transition: all 500ms ease-out;
  -webkit-transition: all 500ms ease-out;
}

.tui-pagination .tui-page-btn:not(:last-child) {
  margin-right: 4px;
}

.tui-pagination .tui-page-btn:hover {
  color: #ffffff;
  background-color: rgba(255, 107, 8, 1);
  box-shadow: 0px 4px 4px 0px #00000040;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  font-weight: 500;
  cursor: default;
  color: #ffffff;
  background-color: rgba(255, 107, 8, 1);
}

.tui-pagination .tui-is-selected:hover {
  background-color: rgba(255, 107, 8, 1);
  box-shadow: 0px 4px 4px 0px #00000040;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
  border-left: none;
  
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: none;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 40px;
  padding: 0;
  font-size: 0;
  line-height: 40px;
  white-space: nowrap;
  font-weight: normal;
  border: none;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 4px;
}

.tui-pagination .tui-next {
  margin-left: 4px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  vertical-align: top;
  background-position: 0px -24px;
  margin-top: 19px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  text-align: center;
  border: 0;
  outline: none;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

@media screen and (min-width: 768px) {
  .tui-pagination {
    margin-top: 50px;
  }
}

@media screen and (min-width: 1024px) {
  .tui-pagination {
    margin-top: 46px;
  }
}
